html,
body,
#root {
  height: 100%;
  width: 100%;
  background: lightblue;
  flex-direction: column;
  display: flex;
}

body {
  font-family: system-ui, sans-serif;
  min-height: 100vh;
  margin: 0;
}

*,
*:after,
*:before {
  box-sizing: border-box;
}

.flex {
  display: flex;
  align-items: center;
}

.flex.fill {
  height: 100%;
}

.flex.center {
  justify-content: center;
}

.topdeck {
  position: absolute;
  width: 100vw;
  height: 33vh;
  top: 0;
}

.bottomdeck {
  position: absolute;
  width: 100vw;
  height: 33vh;
  top: 66vh;
}

.middlecard {
  position: absolute;
  width: 100vw;
  height: 33vh;
  top: 33vh;
}